  background-color: #098248;
}

.alphainCorrect{
  background-color: #CB3434;
}


.flex-parent {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 10px;
}

.flexbox {
  display: flex;
  flex-direction: column;

  height: 100%;
  width: 100%;
  flex-wrap: wrap;
  min-width: 100px;

  overflow: hidden;

  margin: 0 10;
  padding: 15px
}

#buttons {
  margin: 0;
  text-align: center;
}

.relative {
  padding: 10px;
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  background-color: #fff;
}

.navitem {
  width: 100px;
  height: 50px;
  padding: 10px;
